.book {
  perspective: 1400px;
  color: #000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 300px;
  margin: auto;
  display: flex;
  position: relative;
  box-shadow: 1px 1px 12px #000;
}

.cover {
  cursor: pointer;
  transform-origin: 0;
  background-color: #fbfbfb;
  border: 4px solid #a7d8f2;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
}

.book:hover .cover {
  transition: all .5s;
  transform: rotateY(-80deg);
}

.book h3 {
  text-align: justify;
  font-size: 22px;
}

.book p {
  text-align: justify;
  font-size: 16px;
}

.cover img {
  border-radius: 5px;
  width: 100%;
  height: 100%;
}
